@mixin base() {

    color: white;
    font-size: 1.2rem;
    margin: 0 auto;
    padding: 0;
    text-align: center;
    text-decoration: none;
    flex-basis: auto;
    flex-grow: 1;
    flex-shrink: 0;
    width: 100%;
    
    :last-child {
        border-right: none;
    }

    @media all and (min-width: 992px) {
        margin: 0;
        text-align: initial;
        width: initial;
    }

}

.footer-nav {
    @include base;

    @import 'item';
    @import 'link';
    @import 'list';
}